.header {
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 15;
  display: flex;
  background-color: var(--color-text-primary);
  align-items: center;
  padding: var(--offset) calc(var(--offset) * 2);
  transition-duration: 0.5s;
  transition-property: transform, background-color;
}
@media (max-width: 1180px) {
  .header {
    padding: var(--offset);
  }
}
@media (max-width: 999px) {
  .header {
    background-color: #e4e4e4;
  }
}

.nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}
.nav__logo {
  display: flex;
  align-items: center;
  gap: calc(var(--offset) * 0.7);
  margin-right: 0.5em;
  transition-duration: 0.5s;
  transition-property: opacity;
}
.nav__logo:hover, .nav__logo:focus, .nav__logo:focus-within {
  opacity: 0.6;
}
.nav__logo img {
  width: calc(var(--offset) * 10);
  height: calc(var(--offset) * 2.5);
}
.nav__list {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-left: 3em;
  margin-right: auto;
}
@media (max-width: 1380px) {
  .nav__list {
    gap: 1em;
  }
}
@media (max-width: 1280px) {
  .nav__list {
    margin: 0 auto;
  }
}
@media (max-width: 999px) {
  .nav__list {
    margin: 0;
    order: 5;
  }
}
.nav-lang {
  position: relative;
  margin-left: calc(var(--offset) * 0.5);
  font-weight: 600;
  font-size: var(--fz);
  letter-spacing: -0.04em;
  color: var(--color-text-secondary);
  border-radius: 50%;
  background-color: #f5f5f5;
  box-shadow: 0 0 0 0.05em var(--color-text-primary);
  transition-duration: 0.5s;
  transition-property: color, box-shadow;
}
.nav-lang:hover, .nav-lang:focus {
  color: var(--color-text-secondary);
  box-shadow: 0 0 0 0.05em var(--color-text-secondary);
}
.nav__register {
  margin-left: calc(var(--offset) * 0.5);
  background-color: var(--color-text-secondary);
  border-radius: 2em;
  padding: 0.5em 1em !important;
  color: var(--color-text-primary);
}
.nav__register:hover, .nav__register:focus {
  color: var(--color-text-secondary);
  background-color: var(--color-text-primary);
  box-shadow: 0 0 0 0.05em var(--color-text-secondary);
}
.nav__login {
  margin-left: calc(var(--offset) * 1);
}
@media (max-width: 1080px) {
  .nav__login {
    margin-left: 0.5em;
  }
}
.nav__member {
  margin-left: 0.5em;
  display: flex;
  align-items: center;
  gap: 0.5em;
  padding: 0.5em 0.7em !important;
  border-radius: 2em;
  color: #0f171f !important;
  box-shadow: 0 0 0 0.05em #0f171f;
  font-style: italic;
  font-weight: 600;
  font-size: calc(var(--fz) * 0.8);
  position: relative;
  z-index: 1;
  transition-property: box-shadow !important;
  transition-duration: 0.5s;
  min-width: fit-content;
}
@media (max-width: 1180px) {
  .nav__member {
    padding: 0.2em !important;
  }
}
.nav__member picture {
  display: none;
}
@media (max-width: 1180px) {
  .nav__member picture {
    display: block;
  }
}
@media (max-width: 999px) {
  .nav__member picture {
    display: none;
  }
}
.nav__member img {
  display: none;
  width: calc(var(--offset) * 1.75);
  height: calc(var(--offset) * 1.75);
  object-fit: cover;
  border-radius: 50%;
  overflow: hidden;
}
@media (max-width: 1180px) {
  .nav__member img {
    display: block;
  }
}
@media (max-width: 999px) {
  .nav__member img {
    display: none;
  }
}
@media (max-width: 1180px) {
  .nav__member b {
    display: none;
  }
}
@media (max-width: 999px) {
  .nav__member b {
    display: block;
    padding: 0 0.5em;
  }
}
.nav__member:hover, .nav__member:focus, .nav__member:focus-within {
  color: var(--color-text-secondary) !important;
  box-shadow: 0 0 0 0.05em var(--color-text-secondary) !important;
}
.nav__link {
  font-weight: 400;
  font-size: var(--fz);
  letter-spacing: -0.03em;
  color: #0b1117;
  transition-duration: 0.5s;
  transition-property: color;
  min-width: fit-content;
  white-space: nowrap;
  padding: 0.5em 1em;
}
@media (max-width: 1280px) {
  .nav__link {
    padding: 0.3em 0.7em;
  }
}
@media (max-width: 999px) {
  .nav__link {
    font-size: calc(var(--fz) * 1.3);
  }
}
.nav__link.current {
  border-bottom: 1px solid #0b1117;
}
.nav__link:hover, .nav__link:focus, .nav__link:focus-within {
  color: var(--color-text-secondary);
}

@media (min-width: 999px) {
  .nav__link-red {
    background-color: var(--color-text-secondary);
    color: var(--color-text-primary);
    border-radius: 2em;
    transition-property: background-color, color, box-shadow;
    transition-duration: 0.5s;
  }
  .nav__link-red:hover, .nav__link-red:focus, .nav__link-red:focus-within {
    background-color: var(--color-text-primary);
    color: var(--color-text-secondary);
    box-shadow: 0 0 0 0.05em var(--color-text-secondary);
  }
}

.btn-white {
  padding: calc(var(--offset) * 0.8);
}

.dropdown {
  display: flex;
  flex-direction: column;
  gap: 0.5em;
  position: absolute;
  z-index: 10;
  bottom: -170%;
  left: 50%;
  transform: translateX(-50%);
  border-radius: 1em;
  padding: calc(var(--offset) * 0.8);
  box-shadow: 0 0 0 0.05em var(--color-text-secondary);
}
.dropdown-item {
  font-weight: 600;
  font-size: var(--fz);
  letter-spacing: -0.04em;
  color: var(--color-text-secondary);
}
.dropdown-link {
  transition-duration: 0.5s;
  transition-property: color;
}
.dropdown-link:hover, .dropdown-link:focus, .dropdown-link:focus-within {
  color: #0f171f;
}

.profile {
  display: flex;
  gap: var(--offset);
  align-items: center;
  margin: 0 calc(var(--offset) * 2);
  transition-duration: 0.5s;
  transition-property: opacity;
}
@media (max-width: 1380px) {
  .profile {
    margin: 0 1em;
  }
}
.profile:hover, .profile:focus, .profile:focus-within {
  opacity: 0.7;
}
.profile img {
  width: calc(var(--offset) * 2.5);
  height: calc(var(--offset) * 2.5);
  border-radius: 50%;
  overflow: hidden;
  box-shadow: 0 0 0.05em var(--color-text-primary);
  object-fit: cover;
}
.profile__info {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 0.2em;
  padding: 0.5em 0;
}
.profile__info b {
  font-weight: 500;
  font-size: var(--fz);
  letter-spacing: -0.05em;
  color: #0f171f;
}
.profile__info span {
  font-weight: 400;
  font-size: var(--fz);
  color: #787b83;
}
@media (max-width: 1280px) {
  .profile__info {
    display: none;
  }
}
@media (max-width: 999px) {
  .profile__info {
    display: flex;
  }
}

@media (max-width: 999px) {
  .profile-header .profile__info {
    display: none;
  }
}

.header-hide {
  transform: translateY(-100%);
}

.scroll-header {
  background-color: #e4e4e4;
}

.nav__register {
  color: var(--color-text-primary);
  transition-property: background-color, box-shadow, color;
}

.nav-menu {
  width: 100%;
  display: flex;
  align-items: center;
}

.nav__btn {
  display: none;
  border: none;
  background-color: #f5f5f5;
  border-radius: 50%;
  width: calc(var(--offset) * 2.4);
  height: calc(var(--offset) * 2.4);
  padding: 0.6em 0.4em;
  transition: transform 0.5s ease;
  transform: scale(1) translateZ(0);
  will-change: transform;
  overflow: hidden;
  backface-visibility: hidden;
  z-index: 5;
  position: relative;
}
.nav__btn > span {
  position: relative;
  width: 80%;
  height: 70%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.nav__btn > span::after, .nav__btn > span::before {
  content: "";
  display: block;
  width: 100%;
  height: 0.15em;
  border-radius: 5em;
  background-color: var(--color-text-secondary);
  position: absolute;
  transition: background-color 0.5s, top 0.3s ease 0.3s, bottom 0.3s ease 0.3s, transform 0.3s ease;
}
.nav__btn > span::after {
  top: 0;
}
.nav__btn > span::before {
  bottom: 0;
}
.nav__btn > span > span {
  display: block;
  width: 100%;
  height: 0.15em;
  border-radius: 1rem;
  background-color: var(--color-text-secondary);
  transition: background-color 0.5s, opacity 0.3s ease;
}
@media (max-width: 999px) {
  .nav__btn {
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
  }
}

.nav__btn.open > span::after {
  top: 50%;
  transform: translateY(-50%) rotate(45deg);
  transition: top 0.3s ease, transform 0.3s ease 0.3s;
}

.nav__btn.open span::before {
  bottom: 50%;
  transform: translateY(50%) rotate(-45deg);
  transition: bottom 0.3s ease, transform 0.3s ease 0.3s;
}

.nav__btn.open span > span {
  opacity: 0;
  transition: opacity 0.3s ease;
}

.nav__btn:hover {
  transform: scale(0.95);
}

@media (max-width: 999px) {
  .nav-menu {
    position: fixed;
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 1em;
    height: 100%;
    align-items: flex-start;
    padding: 1.5em 0;
    top: 0;
    z-index: 20;
    left: 0;
    margin-top: calc(var(--offset) * 4.5);
    background-color: var(--color-text-primary);
    overflow-y: auto;
    transform: translateX(-100%);
    transition-property: transform;
    transition-duration: 0.5s;
    overflow-y: auto;
  }
}

.nav-menu.open {
  transform: translateX(0);
}

.nav__member {
  width: fit-content !important;
}

@media (max-width: 999px) {
  .nav__list {
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
    gap: 0;
    margin-top: 2em;
  }
}

body {
  max-width: 100vw;
}

body.open {
  overflow: hidden;
}

.nav-menu__footer {
  display: none;
}
.nav-menu__footer .footer-contacts {
  margin: 0;
}
@media (max-width: 999px) {
  .nav-menu__footer {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    padding-left: var(--offset);
    padding-right: var(--offset);
    order: 10;
    margin-top: auto;
    width: 100%;
    margin-bottom: calc(var(--offset) * 10);
  }
}

@media (max-width: 999px) {
  .nav__item {
    width: 100%;
  }
}

@media (max-width: 999px) {
  .nav__link {
    width: 100%;
    display: block;
    border-bottom: 0.03em solid #746e6e;
    padding: 0.5em 0;
    padding-left: var(--offset);
  }
  .nav__link:hover, .nav__link:focus {
    background-color: #f5f5f5;
  }
}

.bottom-nav-menu {
  display: none;
  position: fixed;
  z-index: 30;
  bottom: 0;
  width: 100%;
  padding: var(--offset);
  background-color: #e4e4e4;
  border-top-right-radius: 1em;
  border-top-left-radius: 1em;
}
.bottom-nav-menu ul {
  width: 100%;
  display: flex;
  align-items: flex-start;
  gap: 1em;
}
@media (max-width: 580px) {
  .bottom-nav-menu ul {
    gap: 0.5em;
  }
}
.bottom-nav-menu ul li {
  flex: 1;
  height: 100%;
}
.bottom-nav-menu ul li a {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  gap: 0.5em;
  min-height: 4em;
  height: auto;
  color: #524d4d;
}
.bottom-nav-menu ul li a.current {
  color: var(--color-text-secondary);
}
.bottom-nav-menu ul li a.current svg {
  fill: var(--color-text-secondary);
}
.bottom-nav-menu ul li a svg {
  fill: #524d4d;
  width: 1.8em;
  height: 2em;
}
.bottom-nav-menu ul li a span {
  display: block;
  margin-top: auto;
  text-align: center;
  font-size: calc(var(--fz) * 0.8);
  font-weight: 700;
}
@media (max-width: 580px) {
  .bottom-nav-menu ul li a span {
    font-size: calc(var(--fz) * 0.7);
  }
}
@media (max-width: 999px) {
  .bottom-nav-menu {
    display: block;
  }
}

.nav-list__icon.people {
  width: 2em;
}

.search-goods__head {
  margin-bottom: var(--offset);
}

.footer {
  background-color: #e4e4e4;
  border-top-left-radius: var(--offset);
  border-top-right-radius: var(--offset);
  overflow: hidden;
  padding: var(--offset-s);
  display: flex;
  align-items: flex-start;
  gap: var(--offset);
  position: relative;
  z-index: 1;
}
@media (max-width: 999px) {
  .footer {
    display: none;
  }
}
.footer-contacts {
  display: flex;
  flex-direction: column;
  gap: var(--offset);
  margin-left: auto;
}
.footer-contacts b {
  font-weight: 500;
  font-size: calc(var(--fz) * 1.6);
  line-height: 119%;
  letter-spacing: -0.03em;
  color: #0b1117;
}
.footer-contacts a {
  max-width: 80%;
  font-weight: 600;
  font-size: calc(var(--fz) * 1.2);
  line-height: 119%;
  letter-spacing: -0.02em;
  color: #0b1117;
}
.footer-links {
  display: flex;
  flex-direction: column;
  gap: calc(var(--offset) * 0.8);
}
