    grid-template-columns: 25% 75%;
  }
}
@media (max-width: 680px) {
  .search-tab__row {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 0.5em;
  }
}

.search-tab__send {
  margin: 0;
  margin-left: auto;
}

.send-offer-select {
  width: 100%;
}
.send-offer-select select {
  width: 100%;
  padding: 0.5em;
  box-shadow: 0 0 0 0.1em #c8c8c8;
  border-radius: 0.5em;
  background-position: 98%;
}

.send-offer__bland-row {
  display: grid;
  grid-template-columns: 50% 25%;
  gap: 1em;
  margin-bottom: 0.5em;
}
.send-offer__bland-row input {
  width: 100%;
  border: 0.1em solid #c8c8c8;
  padding: 0.2em;
  border-radius: 0.3em;
  background-color: inherit;
}
.send-offer__bland-row select {
  width: 100%;
  border: 0.1em solid #c8c8c8;
  border-radius: 0.3em;
  padding: 0.1em;
  max-width: calc(var(--offset) * 4);
}

.send-offer__params {
  display: grid;
  grid-template-columns: 40% 40%;
  margin-bottom: 0.5em;
}
.send-offer__params input {
  width: 100%;
  border: 0.1em solid #c8c8c8;
  padding: 0.2em;
  border-radius: 0.3em;
  background-color: inherit;
}

.send-offer__params-numbers {
  width: 100%;
  display: flex;
  align-items: center;
  gap: 1em;
}

.send-offer__params select {
  box-shadow: 0 0 0 0.05em #c8c8c8;
  border-radius: 0.3em;
}

.send-offer-checkbox::before {
  content: "";
  display: none;
  width: 1em;
  height: 1em;
  background-color: var(--color-text-secondary);
  background-image: url(../images/svg/check-white.svg);
  background-position: center;
  background-size: 0.6em;
  background-repeat: no-repeat;
}

.send-offer-check {
  padding: 0;
}

.add-data-wrapper {
  margin-top: 2em;
  max-width: 40%;
  display: flex;
  flex-direction: column;
  gap: 0.5em;
}
.add-data-wrapper h3 {
  margin-bottom: 1em;
}

.search-city {
  width: 100%;
  margin-bottom: 1em;
}
.search-city input {
  width: 100%;
  display: flex;
  flex-direction: column;
  border: 0.1em solid #c8c8c8;
  border-radius: 2em;
  padding: 0.5em;
}
.search-city span {
  color: red;
  font-size: calc(var(--fz) * 0.7);
}

.add-data-wrapper p {
  font-size: calc(var(--fz) * 0.7);
}
.add-data-wrapper select {
  border: none;
  background-color: none;
  color: #c8c8c8;
}

.add-data-row {
  display: flex;
  gap: 1em;
  margin-bottom: 0.5em;
}
.add-data-row select {
  border-radius: 2em;
  border: 0.1em solid #c8c8c8;
  padding: 0.5em;
  color: #0b1117;
}
.add-data-row input {
  width: 100%;
  border: 0.1em solid #c8c8c8;
  border-radius: 2em;
  padding: 0.5em;
}

.add-data__contacts {
  margin-top: 1em;
}

.add-data__contacts__wrapper {
  margin-top: 1em;
  display: grid;
  grid-template-columns: 80% 20%;
  gap: var(--offset);
}

.add-data__row {
  background-color: none;
}

.add-data-checkbox {
  position: relative;
  color: #c8c8c8;
  font-size: calc(var(--fz) * 0.8);
  margin-bottom: 1em;
  display: block;
  cursor: pointer;
}
.add-data-checkbox::after {
  width: 1em;
  height: 1em;
  content: "";
  display: block;
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  border: 0.1em solid #c8c8c8;
  z-index: 0;
}
.add-data-checkbox::before {
  width: 1em;
  height: 1em;
  content: "";
  display: none;
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  background-color: var(--color-text-secondary);
  background-image: url(../images/svg/check-white.svg);
  width: 1em;
  background-repeat: no-repeat;
  background-position: center;
  z-index: 1;
}
.add-data-checkbox input {
  opacity: 0;
}

.add-data-checkbox:has(input:checked)::before {
  display: block;
}

.error-green {
  font-size: calc(var(--fz) * 0.6);
  color: green;
  display: block;
  margin-top: 1em;
}

.label-file {
  padding: calc(var(--offset) * 1.5) var(--offset);
  box-shadow: 0 0 0 0.1em #c8c8c8;
  border-radius: 1em;
  display: flex;
  flex-direction: column;
  gap: 1em;
  cursor: pointer;
}
.label-file b {
  font-weight: 500;
  font-size: calc(var(--fz) * 0.8);
  text-transform: capitalize;
  text-align: center;
  color: #787b83;
}
.label-file span {
  font-weight: 400;
  font-size: calc(var(--fz) * 0.7);
  text-transform: lowercase;
  text-align: center;
  color: #787b83;
}
.label-file input {
  opacity: 0;
  position: absolute;
  z-index: -1;
}

@media (max-width: 1280px) {
  .search-page {
    margin: 0;
    padding: calc(var(--offset) * 2);
    padding-right: var(--offset);
  }
}
@media (max-width: 999px) {
  .search-page {
    padding: var(--offset);
  }
}

.notifications-search {
  right: calc(var(--offset) * 8);
}
@media (max-width: 1280px) {
  .notifications-search {
    right: calc(var(--offset) * 8);
  }
}

.tr {
  display: none !important;
}

.search-page-wrapper {
  display: flex;
  flex-direction: row !important;
  background-color: #f5f5f5;
  padding-top: calc(var(--offset) * 5);
  padding-right: calc(var(--offset) * 2);
}
@media (max-width: 999px) {
  .search-page-wrapper {
    padding: 0;
    padding-top: calc(var(--offset) * 5);
    padding-bottom: calc(var(--offset) * 6) !important;
    flex-direction: column !important;
  }
}

.search-page {
  padding-right: var(--offset);
  padding-left: calc(var(--offset) * 2);
}

.search-page__mark {
  width: calc(var(--offset) * 20);
  height: calc(var(--offset) * 20);
  margin-top: calc(var(--offset) * 3);
  margin-bottom: var(--offset);
  position: sticky;
  border-radius: 1em;
  top: 6em;
  overflow: hidden;
}
@media (max-width: 999px) {
  .search-page__mark {
    max-width: calc(var(--offset) * 30);
    max-height: calc(var(--offset) * 20);
    height: 100%;
    width: 100%;
    margin: 0 auto;
    position: relative;
    top: 0;
    padding: 0;
    margin-bottom: var(--offset);
  }
}
@media (max-width: 480px) {
  .search-page__mark {
    width: 90%;
  }
}
.search-page__mark img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.result-goods__card .mobile-order-head {
  background-color: transparent !important;
  padding-left: 0;
}

.search-inner-wrapper {
  padding-top: calc(var(--offset) * 4);
  padding-bottom: calc(var(--offset) * 6) !important;
}

@media (max-width: 999px) {
  .add-goods__inner {
    display: flex;
    flex-direction: column-reverse;
    margin-bottom: var(--offset-s);
  }
}

@media (max-width: 780px) {
  .search-goods__right-inner {
    display: flex;
  }
}
@media (max-width: 580px) {
  .search-goods__right-inner {
    display: flex;
  }
}
@media (max-width: 480px) {
  .search-goods__right-inner {
    display: flex;
  }
}

.banner__mark {
  width: 100%;
  max-width: 70vw;
  max-height: calc(var(--offset) * 6);
  overflow: hidden;
  border-radius: 1em;
  margin-bottom: 1em;
}
@media (max-width: 780px) {
  .banner__mark {
    max-width: 100%;
    max-height: calc(var(--offset) * 8);
  }
}
.banner__mark img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.go-back {
  border: none;
  background-color: #f5f5f5;
  border-radius: 50%;
  width: calc(var(--offset) * 2.5);
  height: calc(var(--offset) * 2.5);
  padding: 0.6em;
  display: flex;
  align-items: center;
  justify-content: center;
}
.go-back:hover svg, .go-back:focus svg {
  fill: var(--color-text-secondary);
}
.go-back svg {
  width: var(--offset);
  height: var(--offset);
  fill: #0b1117;
  transition-duration: 0.5s;
  transition-property: fill;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  list-style: none;
  text-decoration: none;
}

body {
  background-color: var(--color-text-primary);
  color: var(--color-text-black);
  font-family: var(--ffI), sans-serif;
  touch-action: manipulation;
  user-select: auto;
  font-size: var(--fz);
  position: relative;
  min-height: 100vh;
}
body.modal-open {
  overflow: hidden;
}

:root {
  /* colors */
  --color-default-black: #000;
  --color-bg-primary: #0b1117;
  --color-bg-secondary: #141f2a;
  --color-icon: #353b3d;
  --color-text-primary: #fff;
  --color-text-secondary: #fc4739;
  --color-text-black: #000;
  --color-text-dark: #353b3d;
  --color-text-gray: #979eb2;
  --ffI: "Inter";
  --fz: max(calc(1 * 1vw), 15px);
  --offset: max(calc(1.25 * 1vw), 15px);
  --offset-xxxl: calc(var(--offset) * 10);
  --offset-xxl: calc(var(--offset) * 8);
  --offset-xl: calc(var(--offset) * 7);
  --offset-l: calc(var(--offset) * 5.5);
  --offset-m: calc(var(--offset) * 5);
  --offset-s: calc(var(--offset) * 4);
}

* {
  box-sizing: border-box;
}
*:focus {
  outline: none;
}
* *::before,
* *::after {
  box-sizing: inherit;
}

::selection {
  background-color: #d3d9e2;
  color: #45478d;
  -webkit-text-fill-color: #45478d;
}

::-webkit-scrollbar {
  width: 0 !important;
  height: 0 !important;
  background-color: transparent;
}

::-webkit-scrollbar-thumb {
  border-radius: 50px;
}

.container {
  padding: 0 var(--offset);
  max-width: 780px;
  margin: 0 auto;
  width: 100%;
  overflow: hidden;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  user-select: text;
  word-break: break-word;
}

p {
  user-select: text;
  word-break: break-word;
}

details {
  display: block;
}

summary {
  list-style: none;
  cursor: pointer;
}

summary::-webkit-details-marker {
  display: none;
}

a {
  text-decoration: none;
  color: inherit;
  outline: none;
  text-wrap: balance;
  user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  -webkit-tap-highlight-color: transparent;
  touch-action: manipulation;
  outline: none;
}

img {
  display: block;
}

button {
  background-color: transparent;
  border: none;
  cursor: pointer;
  font-size: var(--fz);
  padding: 0;
  font-family: "Inter", sans-serif;
  user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  -webkit-tap-highlight-color: transparent;
  touch-action: manipulation;
  outline: none;
}
button svg,
button span {
  pointer-events: none;
}

input {
  font-family: var(--ffP), sans-serif;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

textarea {
  resize: none;
}

html {
  scroll-behavior: smooth;
}

[hidden],
[type=hidden],
.visually-hidden,
.tns-visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.main-wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  width: 100vw;
}
@media (max-width: 999px) {
  .main-wrapper {
    padding-bottom: calc(var(--offset) * 3);
  }
}

.footer {
  flex: 1;
}